@font: 'Thabit';

.changePass-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: var(--background-color-elevation-15);
}

.changePass-card {
  box-sizing: border-box;
  width: 427px;
  // height: 510px;
  background: var(--background-color);
  border: 1px solid var(--background-color);
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-items: center;
  justify-content: flex-start;
  padding: 30px;
}

.changePass-title-text {
  font-family: @font;
  //font-style: normal;
  font-weight: 200;
  font-size: 40px;
  line-height: 52px;
  color: var(--primary-color);
  text-align: center;
  span {
    font-weight: 600;
  }
}

.changePass-title-sub-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.changePass-signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--foreground-color);
}
.changePass-signup-instead {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: var(--primary-color);
  cursor: pointer;

  span {
    color: var(--foreground-color);
    cursor: default;
  }
}

.changePass-instructions {
  font-family: Inter;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: 5px;
  color: var(--foreground-color);
}

.changePass-input-heading {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-top: 20px;
}
.changePass-email {
  background: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
}

.changePass-checkbox-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.changePass-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.button-signup {
  cursor: pointer;
}

.changePass-button-login {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  margin-top: 30px;

  width: 326px;
  height: 43px;

  border: 1px solid var(--primary-color);
  border-radius: 3px;
  cursor: pointer;
}
.changePass-signup-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  color: #f2f2f2;
  margin-bottom: 0px !important;
  cursor: pointer;
}

.changePass-terms-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--foreground-color);
  }
  .ttext {
    margin-right: 20px;
    cursor: pointer;
  }
  .ptext {
    cursor: pointer;
  }
}
.changePass-checkbox-stay-signed-in {
  cursor: pointer;
}

.changePass-prompt-text {
  font-size: 12px;
}
