.collection-path-navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  color: white;

  .row {
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .mapping-name {
    font-weight: bold;
  }

  span.seperator {
    margin: 0px 4px;
  }

  .back-btn {
    opacity: 0.6;
    color: white;

    &:hover {
      opacity: 1;
    }
  }
}

.collection-path-navbar-text-overflow {
  max-width: 25ch; // Uses ch unit to limit visibility of atmost ~25 characters.
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--breadcrumb-font-color);

  @media only screen and (max-width: 1270px) {
    max-width: 16ch;
  }

  @media only screen and (max-width: 1090px) {
    max-width: 10ch;
  }
}
