.auto-map-tree-recommendation-container {
  //   border: 2px solid orange;
  height: 100vh;
}

.automap-title {
  //   border: 2px solid yellow;
  position: relative;
  background: var(--background-color);
  z-index: 300;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}

.automap-tree-schema {
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-switcher {
              // dont show switcher icon
              display: none;
            }

            .ant-tree-node-selected {
              // no bg on selected nodes
              background: none;
            }
          }
        }
      }
    }
  }
}
