@tablet: ~'only screen and (max-width: 1000px)';

.header-panel {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: var(--header-background-color);
  // box-shadow: 0px 4px 18px 0px rgba(0, 0, 0, 0.21);

  .right {
    margin-right: 10px;
  }

  .center {
    width: 100%;
    display: flex;
    align-items: center;
  }

  .menu-wrapper {
    display: flex;
    gap: 6px;
    align-items: center;

    .toggle-mapped-icons {
      width: 30px;
      margin: 10px;
      cursor: pointer;
    }
    .icon-button {
      margin-top: 1px;
      color: var(--toolbar-icon-color);
    }

    .icon-separator {
      display: flex;
      margin-top: 1px;
      opacity: 0.2;
      div {
        height: 20px;
        width: 1px;
        background-color: black;

        &:last-child {
          background-color: white;
        }
      }
    }

    .toggle-mapped-icons-disabled {
      width: 30px;
      margin: 10px;
      cursor: not-allowed;
    }

    .menu-item {
      display: flex;
      align-items: center;
      border: none;
      background: none;
      margin: 6px 0px;
      color: white;
      font-size: 13px;
      border-radius: 4px;
      transition: 0.2s background ease;
      cursor: pointer;
      padding: 6px;

      &:hover {
        background: #0000002d;
      }

      &:active {
        background: #0000003a;
      }

      &.pressed {
        background: #0000003a;
      }
    }
  }

  .ivoyant-logo {
    width: 40px;
    padding: 10px;
    background: var(--header-ivoyant-background-gradient);
  }

  .undo-redo-btn > button[disabled] {
    opacity: 0.5;
  }

  .mapping-toggle-btn > button[disabled] {
    color: fade(#fff, 35%);
  }
}

.updated-at {
  @media @tablet {
    display: none;
  }
}
