.card-create-option {
  transform: scale(0.95);
  opacity: 90%;
}

.card-create-option:hover {
  transform: scale(1);
  cursor: pointer;
  background-color: var(--primary-color-opacity-5);
  opacity: 100%;
}

.card-create-option-disabled {
  transform: scale(0.95);
  opacity: 90%;
  cursor: not-allowed;
}
