.sidebar-comments {
  font-size: 12px;
}

.comment-p-10 {
  padding: 10px 10px 0px 10px;
}

.comments-filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 10px;

  .view-comment-title {
    font-weight: 700;
    margin-right: 3rem;
  }
}

.empty-comment-state {
  display: flex;
  margin-top: 2rem;
  justify-content: center;
}

.add-comment-btn-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .add-comment-btn {
    margin-top: 10px;
  }
}

.comments-view-wrapper {
  .comments-view {
    .single-comment-wrapper {
      padding: 10px;
      margin-bottom: 10px;
      border: 1px solid var(--comment-border-color);
      border-radius: 5px;
      background-color: var(--comment-bg-color);

      .comment-creator {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 10px;
        justify-content: space-between;

        .comment-creator-name {
          font-size: 13px;
          font-weight: 700;
          // ! include below css when we are using name instead of email
          // text-transform: capitalize;
        }
      }

      .comment-body {
        font-size: 13px;
      }

      .comment-actions {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        .comment-created-at {
          font-size: 12px;
        }

        .comment-resolve-btn {
          span {
            cursor: pointer;
            text-decoration: underline;
            font-size: 12px;
            font-weight: 700;
          }
        }
      }
    }
  }
}

.resolved-comment-icon {
  color: rgb(89, 194, 89);
}

.comment-open-tag {
  padding: 1px 5px;
}
