.user-profile-container {
  padding: 25px 15px;
  width: 100%;
  font-size: 14px;
  overflow-y: scroll;

  .user-profile-header {
    font-weight: 700;
    font-size: 18px;
    margin-bottom: 35px;
    color: var(--foreground-color);
  }

  .user-profile-form-container {
    width: 70%;

    .user-profile-form-section {
      .user-profile-form-section-header {
        font-weight: 700;
        font-size: 16px;
      }

      .user-profile-form-section-divider {
        border-top: 1px solid var(--panel-border-color);
        margin-top: 0;
      }

      .user-profile-form-section-field {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        width: 100%;
        margin-bottom: 20px;
      }

      .change-password-form-container {
        width: 60%;

        span {
          font-weight: 700;
        }

        .change-password-form-field {
          margin-bottom: 20px;
        }

        .change-password-buttons-container {
          display: flex;
          flex-direction: row;
          align-items: center;
          gap: 20px;
        }
      }
    }
  }
}

.user-profile-form-buttons {
  width: 20%;
  background-color: var(--primary-color);
}

.breadcrumb-link {
  font-size: 12px;
}
