.fd-row {
  background-color: var(--primary-color-opacity-10);
}

.displayText {
  margin: auto 0%;
  font-weight: 400;
  font-size: small;
}

.numberDiv {
  font-size: medium;
  font-weight: bold;
  background-color: #daf1fb;
  border: solid 1px #3c7893;
  border-radius: 50%;
  text-align: center;
  transform: scale(0.7);
}

.viewIcon {
  font-weight: bold;
}

.viewIcon:hover {
  color: #00b2ff;
  background-color: transparent;
}

.deleteIcon:hover {
  color: var(--danger-color);
  background-color: transparent;
}

.grabbable:hover {
  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
}

/* (Optional) Apply a "closed-hand" cursor during drag operation. */
.grabbable:active {
  cursor: grabbing;
}

input.schema_name {
  border: none;
  background: var(--primary-color-opacity-10);
  max-width: 92%;
}

input.schema_name:focus {
  outline: none;
  background: var(--primary-color-opacity-0);
}

.transparent-grey-bg {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 20%;
  margin: 1px;
  display: flex;
  justify-content: center;
}

.filecard-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.file-card-file-icon {
  color: var(--primary-color);
}
