@import '../../styles/lessUtils.less';

.box {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 5px;
  font-size: 12px;

  height: 31px;

  .text-input-mixin();
}

.label {
  width: 86px;
  height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #a7a7a7;
}

.text-area-input-text {
  // width: 100%;
  // height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 10px;
  color: #666666;
  // align-self: flex-end;
}

.edit {
  position: relative;
  left: 0%;
  top: 5%;
}

.edit:hover {
  background-color: transparent;
  color: #00b2ff;
}

.textAreaEditDiv {
  display: inline-flex;
  align-items: center;
  width: 100%;
}

.textAreaTextContainer {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
