.landing-left {
  width: 40%;
  height: 100%;
  mix-blend-mode: normal;
  box-shadow: 4px 0px 8px rgba(255, 255, 255, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1 solid;
  background-color: var(--background-color-elevation-5);
}
.landing-right {
  width: 60%;
  height: 100%;
  background-color: var(--background-color);
}
.landing-container {
  display: flex;
  height: 100%;
}

.landing-title-text {
  font-family: 'Thabit';
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 52px;
  color: #00abf5;
}
.landing-button-signup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;

  width: 326px;
  height: 43px;
  background: #00a4eb;
  border: #00a4eb;
  border-radius: 3px;
  cursor: pointer;
}
.landing-button-login {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  margin-top: 30px;

  width: 326px;
  height: 43px;

  border: 1px solid #00a4eb;
  border-radius: 3px;
  cursor: pointer;
}
.landing-signup-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  color: #f2f2f2;
  margin-bottom: 0px !important;
}
.landing-login-text {
  color: #00a4eb;
}

.landing-skip-text {
  position: absolute;
  bottom: 32px;
  color: var(--foreground-color-opacity-50);

  .skip-link {
    font-weight: bold;
    cursor: pointer;
    text-decoration: underline;
  }
}

.landing-divider {
  display: flex;
  align-items: center;
  text-align: center;
  color: #c2c2c2;
  width: 326px;
  margin-top: 20px;

  &::before,
  &::after {
    content: '';
    flex: 1;
    border-bottom: 1px solid #c2c2c2;
  }
  &::before {
    margin-right: 0.25em;
  }
  &::after {
    margin-left: 0.25em;
  }
}
