.bottom-panel {
  width: 100%;
  position: absolute;
  bottom: 24px;
  background-color: var(--background-color-elevation-5);
  outline: 1px solid var(--panel-border-color);
  .panel {
    z-index: 90;
    height: 100px;
  }

  // box-shadow: -4px 0px 15px 0px #0000003b;
}

.bottom-panel-wrapper {
  display: flex;
  flex-direction: column;

  .bottom-panel-area {
    flex: 1;
  }
}

.status-bar {
  width: 100%;
  background-color: var(--background-color-elevation-5);
  position: absolute;
  bottom: 0px;
  z-index: 1000;

  // .expanded {
  // box-shadow: -4px 0px 15px 0px #0000003b;
  // }
}

.options-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0px 10px;
  height: 100%;
}

.left,
.right,
.center {
  display: flex;
  gap: 10px;
}
