.collection-tree-panel {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.collection-tree-icon {
  font-size: 12px;

  &.root {
    font-weight: 700;
  }
}

.coll-tree-empty-container {
  flex-grow: 0.75;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.collection-tree-node {
  font-size: 12px;
  display: flex;
  align-items: center;

  &:hover {
    .hover-three-dot-container-collection {
      visibility: visible;
    }
  }

  &.root {
    font-weight: 700;
  }
}

// ellipsis on Panel close
.collection-tree {
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-node-content-wrapper {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
}

.hover-three-dot-container-collection {
  margin-left: auto;
  visibility: hidden;
}
