.item:hover {
  opacity: 100%;
  cursor: pointer;
}

.item {
  display: flex;
  align-items: center;
  // justify-content: space-between;
  opacity: 50%;
  min-width: 0;
  gap: 3px;

  &.itemSelected {
    background-color: var(--primary-color-opacity-10);
    border-radius: 2%;
  }
}

.collection-item-version-col {
  margin-left: auto;
}

.collection-item-flex-title {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
