@font: 'Thabit';

.login-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--background-color);
}

.login-card {
  box-sizing: border-box;
  width: 427px;
  height: 490px;
  background-color: var(--background-color-elevation-5);
  border: 1px solid var(--background-color);
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-items: center;
  justify-content: flex-start;
  padding: 30px;
}

.login-title-text {
  font-family: @font;
  //font-style: normal;
  font-weight: 200;
  font-size: 40px;
  line-height: 52px;
  color: var(--primary-color);
  text-align: center;
  span {
    font-weight: 600;
  }
}

.login-title-sub-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.login-signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--foreground-color);
}
.login-instead {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;

  a {
    color: var(--primary-color);
    text-decoration: underline;
  }
}
.login-input-heading {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-top: 20px;
}
.login-email {
  background: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
}

.login-checkbox-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.login-stay-signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-bottom: 0px;
  margin-left: 8px;
}
.login-forgot-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  color: var(--primary-color);
  cursor: pointer;
}

.login-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.button-signup {
  cursor: pointer;
}

.login-button-signup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;

  width: 326px;
  height: 43px;
  background: var(--primary-color);
  border: var(--primary-color);
  border-radius: 3px;
  cursor: pointer;
}
.login-button-login {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  margin-top: 30px;

  width: 326px;
  height: 43px;

  border: 1px solid var(--primary-color);
  border-radius: 3px;
  cursor: pointer;
}
.login-signup-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  // color: #f2f2f2;
  margin-bottom: 0px !important;
  cursor: pointer;
}
.login-text {
  color: var(--primary-color);
}
.login-skip-text {
  width: 84px;
  height: 17px;
  position: absolute;
  bottom: 10px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  display: flex;
  align-items: center;
  text-align: center;
  text-decoration-line: underline;

  /* Gray 3 */

  color: #828282;
}

.login-divider {
  display: flex;
  align-items: center;
  text-align: center;
  color: #c2c2c2;
  width: 326px;
  margin-top: 30px;

  &::before,
  &::after {
    content: '';
    flex: 1;
    border-bottom: 1px solid #c2c2c2;
  }
  &::before {
    margin-right: 0.25em;
  }
  &::after {
    margin-left: 0.25em;
  }
}

.login-terms-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--foreground-color);
  }
  .ttext {
    margin-right: 20px;
    cursor: pointer;
  }
  .ptext {
    cursor: pointer;
  }
}
.login-checkbox-stay-signed-in {
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-text-fill-color: var(--foreground-color) !important;
  box-shadow: 0 0 0 30px var(--background-color-elevation-5) inset !important;
  -webkit-box-shadow: 0 0 0 30px var(--background-color-elevation-5) inset !important;
}
