@import '../../../styles/lessUtils.less';

.parent-clipper {
  width: 100%;
}

.red-box {
  height: 100px;
  width: 90%;
  // background: red;
  background: var(--background-color);
  position: absolute;
  z-index: 25;
}

.create-mapping-container {
  padding: 20px;
  width: 100%;
  height: 100%;
  position: relative;

  .create-mapping-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 300;

    // old style
    // .breadcrumb-link-create-mapping {
    //   font-size: 12px;
    //   position: relative;
    //   z-index: 300;
    // }

    .breadcrumb-link-create-mapping {
      position: relative;
      z-index: 300;
      font-weight: 700;
      font-size: 16px;
      color: var(--foreground-color);
    }
  }

  .create-mapping-form-container {
    margin-top: 20px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    // border: 2px solid blue;

    .create-mapping-stepper-container {
      width: 25%;
      // margin-top: 35px;
      // border: 2px solid red;
      position: relative;
      z-index: 300;

      .ant-steps-vertical {
        .ant-steps-item-finish {
          .ant-steps-item-container {
            .ant-steps-item-icon {
              cursor: pointer;
            }
            .ant-steps-item-content {
              cursor: pointer;
            }
          }
        }
      }

      // .create-mapping-step:not(.ant-steps-item-wait) {
      //   .ant-steps-item-container {
      //     .ant-steps-item-icon {
      //       cursor: pointer;
      //     }
      //     .ant-steps-item-content {
      //       cursor: pointer;
      //     }
      //   }
      // }

      .create-mapping-step + .enable-next-step {
        .ant-steps-item-container {
          .ant-steps-item-icon {
            cursor: pointer;
          }
          .ant-steps-item-content {
            cursor: pointer;
          }
        }
      }

      .create-mapping-step + .disable-this-step {
        .ant-steps-item-container {
          .ant-steps-item-icon {
            cursor: not-allowed;
          }
          .ant-steps-item-content {
            cursor: not-allowed;
          }
        }
      }
    }

    .create-mappping-data-form-container {
      width: 100%;

      .set-mapping-name-container {
        height: 100%;
        overflow-y: scroll;
        // border: 2px solid orange;

        .set-mapping-name-title {
          font-size: 14px;
          font-weight: 700;
          text-transform: uppercase;
          position: relative;
          z-index: 300;
        }

        .set-mapping-name-form-container {
          // margin-top: 20px;

          .set-mapping-name-edit-btn {
            cursor: pointer;
          }

          .set-mapping-name-edit-btn:hover {
            color: var(--primary-color);
          }

          .grey-bg-select-box {
            border-radius: 3px;

            .ant-select-selector {
              background-color: var(--input-bg-color);
            }
          }
        }
      }
    }
  }

  .step-content-title {
    font-size: 14px;
    font-weight: 700;
  }

  .step-content-description {
    font-size: 12px;
  }

  .create-mapping-next-button {
    display: flex;
    align-items: center;
    gap: 5px;
    border: none;
  }
}

.automap-top-button-container {
  display: flex;
  flex-direction: row;
  position: relative;
  z-index: 300;
  align-items: center;
  justify-content: space-between;
}

.create-mapping-next-button-text {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
}

.mapper-text-input {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 5px;
  font-size: 14px;

  height: 31px;

  .text-input-mixin();
}

.mapper-text-input-label {
  width: 86px;
  height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #a7a7a7;
}

.map-desc-container {
  display: flex;
  gap: 20px;
}
