.title {
  font-size: small;
  font-weight: bold;
}

.browseBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 10px;
  background: #00abf5;
  border-radius: 5px;
  width: 130px;
}

.fileIcon {
  font-size: 26px;
  background-color: #cfeff6;
  border-radius: 50%;
  padding: 13px;
  color: #2d718e;

  &.disable-fileIcon {
    cursor: not-allowed !important;
    background-color: var(--disabled-bg-color);
    color: darkgray;
  }
}

.create-menu-page {
  height: 100%;
  width: 100%;
  background-color: var(--card-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: scroll;

  .wrapper {
    padding: 16px 12px;
    box-shadow: 0px 0px 15px 0px var(--card-box-shadow-color);
    background: var(--background-color-elevation-5);

    // adding this width because import options removed i.e. steps 3 and 4
    // min-width: 500px;

    .ant-row {
      margin-top: 0px !important;

      // .disable-create-menu-option {
      //   .ant-card {
      //     background-color: var(--foreground-color-opacity-70);
      //   }
      // }

      .ant-col:not(.disable-create-menu-option) {
        .ant-card {
          background: var(--background-color-elevation-5);

          &:hover {
            background-color: var(--primary-color-opacity-5);
          }
        }
      }
    }
  }
}

.disable-create-menu-option {
  .ant-card {
    background: var(--background-color-elevation-5);

    // &:hover {
    // background-color: var(--disabled-bg-color);
    // }
  }
}
