@font: 'Thabit';

.forgetPass-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background: var(--background-color);
}

.forgetPass-card {
  box-sizing: border-box;
  width: 427px;
  height: 410px;
  background-color: var(--background-color-elevation-5);
  // border: 1px solid var(--background-color);
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-items: center;
  justify-content: flex-start;
  padding: 30px;
}

.forgetPass-title-text {
  font-family: @font;
  //font-style: normal;
  font-weight: 200;
  font-size: 40px;
  line-height: 52px;
  color: var(--primary-color);
  text-align: center;
  span {
    font-weight: 600;
  }
}

.forgetPass-title-sub-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.forgetPass-signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--foreground-color);
}
.forgetPass-signup-instead {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: var(--primary-color);
  cursor: pointer;

  span {
    color: var(--foreground-color);
    cursor: default;
  }
}

.forgetPass-instructions {
  font-family: Inter;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: 5px;
  color: var(--foreground-color);
}

.forgetPass-input-heading {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-top: 20px;
}
.forgetPass-email {
  background: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
}

.forgetPass-checkbox-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.forgetPass-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.button-signup {
  cursor: pointer;
  width: 100%;
}

.forgetPass-button-login {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  margin-top: 30px;

  width: 326px;
  height: 43px;

  border: 1px solid var(--primary-color);
  border-radius: 3px;
  cursor: pointer;
}
.forgetPass-signup-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  color: var(--foreground-color);
  margin-bottom: 0px !important;
  cursor: pointer;
}

.forgetPass-terms-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--foreground-color);
  }
  .ttext {
    margin-right: 20px;
    cursor: pointer;
  }
  .ptext {
    cursor: pointer;
  }
}
.forgetPass-checkbox-stay-signed-in {
  cursor: pointer;
}

.forgetPass-success-text {
  margin-top: 40px;
  text-align: center;
  color: var(--foreground-color);

  h3 {
    font-size: 18px;
    font-weight: 700;
  }

  p {
    font-size: 12px;
    font-weight: 400;
    margin-top: 23px;
  }
}

.forgetPass-success-img {
  display: flex;
  justify-content: center;
  padding-right: 15px;
  margin-top: 43px;
  margin-bottom: 96px;
}

.forgetPass-card-success {
  height: 504px;
}

.forgetPass-success-info-text {
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}
