.admin-area {
  height: 100%;
  width: 100%;
  background-color: var(--card-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: scroll;

  .admin-import-schema {
    @media screen and (max-height: 730px) {
      margin-top: 200px;
    }
    padding: 32px;
    box-shadow: 0px 0px 15px 0px var(--card-box-shadow-color);
    background: var(--background-color-elevation-5);

    max-width: 700px;

    .ant-btn {
      text-transform: uppercase;
    }

    h2 {
      text-transform: uppercase;
      font-size: 13px;
      font-weight: bold;
    }

    span {
      font-size: 12px;
      opacity: 0.9;
    }

    input,
    textarea {
      font-size: 13px;
    }

    label {
      font-size: 12px;
    }

    .ant-form-item-label {
      line-height: normal;
      margin: 0px;
      padding: 0px;
    }
  }

  .edit-schema {
    width: 100%;
    height: 100%;
    background: var(--background-color);
    padding: 32px;
  }

  .ant-form-item-explain-error {
    font-size: 12px;
  }
}
