.workspace-wrapper {
  // See https://developer.mozilla.org/en-US/docs/Web/CSS/grid-area
  display: grid;
  height: 100%;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto 1fr auto;
  overflow: hidden;
  box-sizing: border-box;

  .top {
    // See (docs/ui/workspace-grid-reference.png) to make sense
    grid-area: 1 / 1 / 2 / 4;
    z-index: 40;
  }

  .right {
    grid-area: 2 / 3 / 4 / 4;
    z-index: 30;
  }

  .bottom {
    grid-area: 3 / 1 / 4 / 4;
    z-index: 40;
  }

  .left {
    grid-area: 2 / 1 / 4 / 2;
    z-index: 30;
  }

  .center {
    grid-area: 2 / 2 / 4 / 3;
    z-index: 20;
  }
}
