.lookup-table-tree-container {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.lookup-table-tree-node {
  font-size: 12px;
  font-weight: 700;

  // &.root {
  //   font-weight: 700;
  // }
}

.lookup-table-tree-node {
  font-size: 12px;
  display: flex;
  align-items: center;

  &:hover {
    .hover-three-dot-container-Lookup-Table {
      visibility: visible;
    }
  }
}

.hover-three-dot-container-Lookup-Table {
  margin-left: auto;
  visibility: hidden;
}

// ellipsis on Panel close
.lookup-table-tree {
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-node-content-wrapper {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
}

.lookup-table-tree-empty-container {
  flex-grow: 0.75;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
