.title {
  font-size: small;
  font-weight: bold;
}

.applyBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 10px;
  background: #00abf5;
  border-radius: 5px;
  width: 100px;
}

.discardBtn {
  border-radius: 5px;
  width: 100px;
}

.discardBtn:hover {
  border-radius: 5px;
  width: 100px;
  color: var(--danger-color);
  background: transparent;
}
