.tree-search-container {
  display: flex;
  gap: 6px;
  align-items: center;
  border-bottom: 1px solid var(--panel-border-color);
  height: 40px;
  padding-left: 10px;

  .dataMap-history-pane-title {
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }
}

.no-bg-tree {
  background: none !important;

  // .ant-tree-treenode {
  //   .ant-tree-node-content-wrapper:hover,
  //   .ant-tree-node-content-wrapper-close:hover {
  //     background-color: white;
  //   }
  // }

  // dont show switcher icon
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-switcher {
              display: none;
            }
          }
        }
      }
    }
  }
}
.clip-div {
  // background-color: rgba(255, 0, 0, 0.5);
  height: 1px;
  visibility: hidden;

  &.bottom {
    width: 100%;
    bottom: 20px;
    position: absolute;
  }
}

.mapper-loading {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
