@import '../../styles/colors.less';

.schema-mapper {
  width: 100%;
}

.connection-lines-container {
  width: 150px;
  background: rgb(223, 223, 223);
  outline: 1px solid gray;
}

.tree-search-container {
  display: flex;
  gap: 6px;
  align-items: center;
  border-bottom: 1px solid var(--panel-border-color);
  height: 40px;
  padding-left: 10px;

  .schema-mapper-pane-title {
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }
}

.line-endpoint-dot {
  font-size: 18px;
  color: @node-object-icon;
}

.node-container {
  display: flex;
  gap: 3px;
  align-items: center;
}

.tree-node-container {
  padding: 0 4px;
  color: var(--schema-tree-node-text-color);
  height: 24px;
  // overflow-y: hidden;

  &:hover {
    .hover-three-dot-container {
      visibility: visible;
    }
  }
}

.tree-node-error-bg {
  background-color: var(--danger-color-opacity-40);
}

.node-change-name-input-field {
  font-size: 12px;
}

.file-delete-x {
  margin-left: auto;
  cursor: pointer;

  :hover {
    color: var(--danger-color);
  }
}

.hover-three-dot-container {
  margin-left: auto;
  cursor: pointer;
  visibility: hidden;
}

// Common for all .ant-tree-treenode

.ant-tree .ant-tree-node-content-wrapper:hover {
  background-color: var(--node-hover-color);
}

.ant-tree-treenode {
  height: 25px;
  border: 2px solid transparent;
}

.show-debug-lines {
  .ant-tree-treenode {
    border: 2px solid blue;
  }
}

.source-tree-root {
  // dont show switcher icon
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-switcher {
              display: none;
            }
          }
        }
      }
    }
  }
}

.no-bg-tree {
  background: none !important;

  // .ant-tree-treenode {
  //   .ant-tree-node-content-wrapper:hover,
  //   .ant-tree-node-content-wrapper-close:hover {
  //     background-color: white;
  //   }
  // }

  // dont show switcher icon
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-switcher {
              display: none;
            }
          }
        }
      }
    }
  }
}

.no-indent-tree-codeeditors {
  background: none !important;

  // dont show switcher icon and no color on treeNode select
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-switcher {
              display: none;
            }
            .ant-tree-node-selected {
              background-color: transparent !important;
            }
          }
        }
      }
    }
  }

  // no indent & no bg
  .ant-tree-treenode {
    .ant-tree-node-content-wrapper:hover,
    .ant-tree-node-content-wrapper-close:hover {
      cursor: default;
      background: none !important;
    }

    .ant-tree-indent {
      display: none !important;
    }
  }
}

