// clip div style
.clip-div {
  // background-color: rgba(255, 0, 0, 0.5);
  height: 1px;
  visibility: hidden;

  &.bottom {
    width: 100%;
    bottom: 20px;
    position: absolute;
  }
}

// node-required icon
.node-required-icon {
  font-weight: 700;
  color: var(--node-required-icon-color);
}

.schema-loading {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
