.visual-json-editor {
  height: 100vh;
  width: 100%;
  overflow-y: hidden;

  main {
    display: flex;
    height: 100%;
  }

  .left,
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .vje-tree-search-container {
    display: flex;
    gap: 6px;
    align-items: center;
    // border-bottom: 1px solid var(--panel-border-color);
    height: 39px;
    padding-left: 10px;
    justify-content: space-between;

    .vje-schema-mapper-pane-title {
      font-weight: 700;
      font-size: 13px;
      text-transform: uppercase;
    }

    .json-schema-editor-toggle-container {
      margin-right: 20px;
    }
  }

  .visual-editor {
    // To align with textarea
    margin-top: 1px;
    width: 25%;

    &.invisible {
      display: none;
    }

    &.visible {
      display: initial;
    }
  }

  // .node-type-select {
  //   font-size: 12px;
  // }

  .code-editor {
    // margin-top: 2rem;
    font-family: 'Courier New', Courier, monospace;
    font-size: 12px;
    width: 100%;
    flex: 1;
    border: 1px solid var(--panel-border-color);
    background-color: var(--input-bg-color);
  }
}

.code-editor-container {
  height: 100%;
  overflow: scroll;
  position: relative;

  .code-editor-save-btn {
    position: sticky;
    z-index: 90;
    top: 10px;
    display: block;
    margin-bottom: -4rem;
    margin-left: auto;
    margin-right: 1rem;
  }
}

.primary-icon-color {
  color: var(--node-icon-color);
}

.json-editor-json-view-top-actions {
  display: flex;
  justify-content: right;
  gap: 6px;
  margin-right: 1rem;
  margin-top: 1rem;

  .json-view-top-actions-select-wrapper {
    width: 8.5rem;

    .top-actions-select-span {
      color: var(--primary-color) !important;
    }
  }
}

.json-editor-tree-container {
  // height: calc(100vh - 120px);
  border-top: 1px solid var(--panel-border-color);
  height: 100vh;
}

.edit-data-icon {
  color: var(--node-icon-color);
}

.json-tree-node-container {
  .json-tree-node-actions {
    display: flex;
    align-items: center;
    justify-content: right;
    opacity: 0;
  }
  &:hover {
    .json-tree-node-actions {
      opacity: 1;
    }
  }
}

.add-node-drawer-wrapper {
  padding: 0.5rem;
  background-color: var(--json-editor-add-node-drawer-bg);

  .add-node-drawer-select {
    display: block;
    // font-size: 12px;
  }

  .add-node-drawer-label {
    opacity: 0.2;
    font-size: 11px;
  }
}

.json-tree-root {
  // dont show switcher icon
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            height: max-content;
            .ant-tree-switcher {
              margin-top: 5px;
            }
            .ant-tree-node-content-wrapper {
              // display: none;
              width: 100%;
            }
          }
        }
      }
    }
  }
}

.json-crack-graph-iframe {
  height: 100%;
  border: none;
}

.visual-editor-font-12 {
  font-size: 12px;
}

.json-schema-editor-header-icon-container {
  margin-right: 20px;

  .json-schema-editor-header-icon {
    cursor: pointer;
  }

  .json-schema-editor-header-icon:hover {
    color: var(--primary-color);
  }
}

.expand-editor-button {
  position: absolute;
  top: 60vh;
  left: 0;
  z-index: 99;
  background-color: var(--primary-color);
  height: 4.5rem;
  width: 9px;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
