.error-panel {
  height: 100%;
}

.error-fc {
  display: flex;
  align-items: center;
}

.error-list-wrapper {
  margin-top: 10px;
  margin-left: 10px;
  height: 100%;
  overflow: scroll;
}

.error-panel-close-icons {
  border-bottom: 1px solid var(--panel-border-color);
}

.error-panel-close-icon {
  cursor: pointer;

  &:hover {
    color: var(--danger-color);
  }
}
