.dropzone {
  background-color: var(--primary-color-opacity-10);
  border: none;
  font-family: Arial;
  text-align: center;
  padding: 36px 0px;
}

.fileIcon {
  font-size: 32px;
  background-color: var(--primary-color-opacity-15);
  border-radius: 50%;
  padding: 3%;
  color: #2d718e;
  cursor: pointer;
}
