.title {
  font-size: small;
  font-weight: bold;
}

.subtitle {
  font-size: small;
  color: #a7a7a7;
  margin-top: 3%;
}

.label {
  width: 86px;
  height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #a7a7a7;
}

.saveBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 10px;
  background: #00abf5;
  border-radius: 5px;
  width: 100px;
}

.backIcon {
  font-weight: bold;
}

.backIcon:hover {
  color: #00b2ff;
  background-color: transparent;
}
