@desktop: ~'only screen and (min-width: 960px) and (max-width: 2560px)';
@tablet: ~'only screen and (min-width: 720px) and (max-width: 959px)';
@phone: ~'only screen and (min-width: 320px) and (max-width: 720px)';

.modal-container {
  display: flex;
  width: 100%;
  //overflow: scroll;
  //height: calc(100vh - 120px);

  // height: 100%;
}

.tree-container {
  padding: 0.5rem 0.2rem;
  width: 30%;
  flex-grow: 1;
  flex-shrink: 0;
  padding-bottom: 2.5rem;

  @media @phone {
    display: none;
  }
}

.upload-input-group {
  display: flex;
  align-items: center;
}

.form-container {
  margin-top: 1.5rem;
  margin-left: 2rem;
  padding: 0.5rem 0.2rem;
  width: 100%;
  .input-label {
    // color: var(--foreground-color);
    color: rgb(117, 117, 117);
    display: block;
    margin-top: 0.5rem;
  }
  p {
    color: rgb(117, 117, 117);
    display: inline;
  }
}

.upload {
  display: flex;
  align-items: center;
  color: var(--primary-color);
  padding-left: 0.5rem;
  cursor: pointer;
  span {
    text-decoration: underline;
    margin-left: 0.2rem;
    font-weight: 600;
    font-size: 0.8rem;
    text-transform: uppercase;
  }
}

.modal-drop-file {
  margin-top: 0.6rem;
  //height: 260px;
}

.tree {
  height: 100%;
  width: 100%;
  border: 1px solid var(--collection-tree-border-color);
  background-color: var(--collection-tree-background-color);
  padding: 0.2rem 0.5rem;
}

.modal-buttons {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  gap: 10px;

  button {
    border-radius: 0.3rem;
    padding: 0.9rem auto;
    svg {
      margin-right: 0.3rem;
    }
  }
}

.sourceFile-selectOptions-container {
  display: flex;
  align-items: center;

  .sourceFile-key {
    // background-color: #daf1fb;
    background-color: var(--collection-tree-border-color);
    border-radius: 90rem;
    border: 1px solid #3c789363;
    font-weight: 700;
    font-size: 0.8rem;
    height: 1rem;
    width: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.1rem;
    margin-right: 1rem;
  }
  button {
    padding: 0 8px;
  }
  span {
    margin-left: 0.8rem;
    margin-right: 0.8rem;
    font-weight: 300;

    &:nth-of-type(2) {
      margin-left: auto;
    }
    &:first-of-type {
      font-weight: 400;
    }
  }
}

.modal-source-checkBox-container {
  border: 1px #aacbd9 solid;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;

  .single-checkbox {
    display: flex;
    align-items: center;
  }
  .file-details-container {
    display: flex;
    margin-left: auto;
    justify-content: space-between;

    div {
      display: flex;
      align-items: center;
      span {
        font-size: 0.7rem;
        margin: 0 0.8rem;
        font-weight: 300;
      }
      button {
        padding: 0 8px;
      }
      svg {
        color: #4a4a4a;
      }
    }
  }
}

.modal-dotted-line {
  margin: 0.9rem 0;
  border: 2px dotted rgb(109, 109, 109);
  border-style: none none dotted;
}

.check-tests-modal-container {
  display: grid;
  grid-template-columns: 1fr auto 1fr 1fr;
  // grid-template-areas: 1fr;
  height: calc(100vh - 100px);
  overflow: scroll;
  position: relative;
  align-items: stretch;
}

.leftSide-tree-container {
  // width: 30%;
  height: 100%;
  min-width: max-content;
}

.check-test-source-tree {
  height: 100%;
  min-width: max-content;
  background-color: var(--background-color);
  width: 100%;
}

.target-tree-container {
  // width: 30%;
  background-color: var(--collection-tree-background-color);
  height: 100%;
  border: 1px solid var(--collection-tree-border-color);
  min-width: max-content;
  border-right: none;
}

.check-tests-target-tree {
  height: 100%;
  min-width: max-content;
}

.check-test-output {
  // width: 31%;
  border: 1px solid var(--collection-tree-border-color);
  border-left: none;
  background-color: var(--collection-tree-background-color);
  height: 100%;
}

.check-test-output-tree {
  height: 100%;
  min-width: max-content;
}

.collections-modal-upload-text {
  font-size: 12px;
  font-weight: 700;
}

.modals-mini-title {
  font-weight: 700;
  text-transform: uppercase;
}
