@font: 'Thabit';

.full-height-signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--background-color);
  height: 100%;
}

.card {
  box-sizing: border-box;
  width: 857px;
  height: 450px;
  background: var(--background-color-elevation-5);
  border: 1px solid var(--background-color);
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.1);
  flex-direction: row;
  display: flex;
  justify-content: space-evenly;
  align-items: stretch;
  padding: 30px;
}
.left-card {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 20px;
  padding-left: 10px;
  position: relative;
}
.right-card {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  padding-left: 10px;
}

.why-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  color: var(--foreground-color);
  line-height: 22px;
  margin-top: 15px;
  margin-bottom: 0px;
}
.reason-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 15px;
  div {
    width: 10px;
    height: 10px;
    background: #d9d9d9;
    border-radius: 1px;
    margin-right: 10px;
  }
  p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--foreground-color);
    margin-bottom: 0px;
  }
}
.line {
  width: 1px;
  height: 90%;
  position: absolute;
  right: 30px;
  bottom: 10px;
  background: #d9d9d9;
}

.title-text {
  font-family: @font;
  //font-style: normal;
  font-weight: 200;
  font-size: 40px;
  line-height: 52px;
  color: var(--primary-color);
  text-align: center;
  margin-top: 30px;
  span {
    font-weight: 600;
  }
}

.title-sub-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--foreground-color);
}
.signup-instead {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: var(--primary-color);
  //margin-top:30px;
  cursor: pointer;
}
.signup-checkbox {
  cursor: pointer;
}
.input-heading {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-top: 15px;
}
.email {
  background: var(--background-color);
  border: 1px solid var(--background-color-elevation-15);
  border-radius: 3px;
}
.checkbox-main {
  padding-right: 20px;
  padding-bottom: 0;
  padding-top: 10px;
}
.checkbox-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 10px;
}
.stay-signin-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-bottom: 0px;
  margin-left: 8px;
}
.forgot-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  color: var(--primary-color);
}

.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 0px;
}
.terms-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  color: var(--foreground-color);
  margin-left: 15px;
  margin-top: 25px;
  margin-bottom: 25px;
  span {
    color: var(--primary-color);
    text-decoration-line: underline;
    cursor: pointer;
  }
}

.button-signup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  width: 326px;
  height: 43px;
  background: var(--primary-color);
  border: var(--primary-color);
  border-radius: 3px;
}
.button-login {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 8px;
  gap: 10px;
  margin-top: 20px;
  width: 326px;
  height: 43px;
  border: 1px solid var(--primary-color);
  border-radius: 3px;
  cursor: pointer;
}
.signup-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  // color: #f2f2f2;
  margin-bottom: 0px !important;
}
.login-text {
  color: var(--primary-color);
  cursor: pointer;
}
.skip-text {
  width: 84px;
  height: 17px;
  position: absolute;
  bottom: 10px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  text-align: center;
  text-decoration-line: underline;
  color: var(--foreground-color);
}

.divider {
  display: flex;
  align-items: center;
  text-align: center;
  color: var(--foreground-color);
  width: 326px;
  margin-top: 15px;

  &::before,
  &::after {
    content: '';
    flex: 1;
    border-bottom: 1px solid var(--foreground-color);
  }
  &::before {
    margin-right: 0.25em;
  }
  &::after {
    margin-left: 0.25em;
  }
}

.sign_in_text {
  text-decoration: underline;
  text-underline-offset: 2px;
  color: var(--primary-color);
}

.instead-text {
  color: var(--foreground-color);
  cursor: default;
}

.copyright-text {
  position: fixed;
  bottom: 30px;
}
