.file-display-item {
  background-color: var(--primary-color-opacity-10);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  min-height: 30px;
  padding: 0px 10px;
  border-radius: 6px;
  flex-wrap: wrap;

  .left,
  .right,
  .file-type-icon {
    display: flex;
    align-items: center;
  }

  .file-type-icon {
    color: var(--primary-color);
  }

  .count {
    font-size: 12px;
    height: 16px;
    width: 16px;
    background-color: var(--primary-color-opacity-10);
    border: 1px solid var(--primary-color-opacity-70);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .right {
    font-size: 12px;

    .file-size {
      margin-right: 6px;
    }
  }
}
