.full-height {
  height: 100%;
}

.row {
  display: flex;
  flex-direction: row;
}

.jc-stretch {
  justify-content: stretch;
}

.flex-1 {
  flex: 1;
}

// margin
// margin right
.mr-10 {
  margin-right: 10px;
}

// margin left
.ml-10 {
  margin-left: 10px;
}

.ml-20 {
  margin-left: 20px;
}

.ml-30 {
  margin-left: 30px;
}

.ml-auto {
  margin-left: auto;
}

// margin top
.mt-10 {
  margin-top: 10px;
}

.mt-53 {
  margin-top: 53px;
}

// line endpoint dot
.dot-icon-position {
  margin-top: 9px;
}

// scrollable
.scrollable {
  overflow: scroll !important;
}

.hidden-scrollable {
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.overlay-z-wrapper {
  z-index: 50;
  position: relative;
  background: var(--background-color);

  &.grey-bg {
    background: var(--workspace-grey-bg-color);
  }
}
