.node-context-menu {
  background: var(--background-color);
  color: var(--foreground-color);
  padding: 10px 0px;
  box-shadow: 4px 5px 5px 0px #00000013;
  border-radius: 4px;

  ul {
    list-style: none;
    padding: 0px;
    margin: 0px;

    hr {
      margin: 4px 0px;
      padding: 0px;
    }
  }

  .menu-item {
    padding: 4px 16px;
    user-select: none;
    cursor: pointer;
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 5px;

    &.no-click {
      cursor: default;

      &:hover {
        background: var(--background-color);
        color: var(--foreground-color);
      }
    }

    &.disabled {
      color: gray;
      cursor: not-allowed;

      &:hover {
        background: var(--background-color);
        color: gray;
      }
    }

    &:hover {
      background: var(--primary-color);
      color: var(--background-color);
    }
  }

  .cm-node-name {
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 5px;
  }

  .cm-node-name-monospace {
    font-family: monospace;
  }

  .cm-node-name-italic {
    font-style: italic;
    text-decoration: underline;
  }

  .menu-Icon {
    color: var(--node-icon-color);
  }
