  //     height: 30px;

  //     .ant-menu-title-content {
  //       font-size: 12px;
  //     }
  //   }
  // }
}

.menu-container {
  border: 1px solid var(--context-menu-primary-color);
  border-radius: 5px;
  span {
    color: var(--context-menu-primary-color);
  }
}

.menu-container-list {
  border: 1px solid var(--context-menu-primary-color);
  border-radius: 5px;

  hr {
    border-top: 1px solid var(--context-menu-primary-color);
  }
}
