button.status-bar-button {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 13px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  &:focus-visible {
    border: 2px solid var(--foreground-color);
  }

  &:hover {
    background-color: var(--primary-color-opacity-5);
  }

  &:active {
    background: var(--primary-color-opacity-10);
  }

  &.selected {
    border-top: 2px solid var(--primary-color);
  }

  &.danger {
    color: var(--danger-color);
  }

  &.alert {
    color: var(--alert-color);
  }

  &.disabled-cursor {
    cursor: not-allowed !important;
  }
}

.iconify {
  font-size: 16px;
}
