.cancelBtn {
  border-radius: 5px;
  width: 100px;
}

.cancelBtn:hover {
  border-radius: 5px;
  width: 100px;
  color: red;
  background: transparent;
}

.listBorder {
  border: solid 1px #00abf5;
  border-radius: 5px;
  background-color: var(--primary-color-opacity-5);
  margin-top: 32px;
  padding: 12px 6px;
}

.newCollectionBtn:hover {
  background: transparent;
  color: #00b2ff;
}

.newCollectionBtn {
  @media @phone {
    margin-bottom: 1rem;
  }
}
