.language_select {
  display: flex;
  margin-left: 10px;
  align-items: center;
  font-size: 12px;
  border: 2px;
}

.language_type_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.language_select_container {
  display: flex;
  margin-top: 10px;
  margin-right: 10px;
  justify-content: space-between;
  //   align-items: center;
}

.textarea_container {
  margin-top: 20px;
  padding: 0px 10px;

  * {
    font-family: monospace;
  }

  button {
    font-family: Inter, Arial, Helvetica, sans-serif !important;
  }

  ::selection {
    background: var(--primary-color-opacity-10);
  }
}

textarea.ant-input.ant-input-borderless {
  font-size: 12px;
}

.code-generator-pane-title {
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
}

.language_dropdown {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}

span.ant-select-selection-item {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
}

.copy_icon {
  margin-right: 10px;
}

.regen-icon {
  margin-right: 10px;
}

.code-generator-ui-icon {
  cursor: pointer;
  color: var(--foreground-color);
}

.code-generator-ui-icon:hover {
  color: var(--primary-color);
}

.icon_container {
  display: flex;
  align-items: center;
  // justify-content: flex-end;
  margin-left: auto;
  gap: 5px;
  flex-wrap: wrap;
}

.generate-placeholder {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0px;
}
