.admin-schema-tree-container {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.admin-schema-tree-node {
  font-size: 12px;

  &.root {
    font-weight: 700;
  }
}

// ellipsis on Panel close
.admin-schema-tree {
  .ant-tree-list {
    .ant-tree-list-holder {
      div {
        .ant-tree-list-holder-inner {
          .ant-tree-treenode {
            .ant-tree-node-content-wrapper {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
}

.admin-schema-tree-empty-container {
  flex-grow: 0.75;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
