.sidebar {
  display: flex;
  background-color: var(--background-color-elevation-5);
  outline: 1px solid var(--panel-border-color);

  .sidebar-items-icons {
    margin-top: 20px;
    width: 40px;
  }

  .sidebar-item-icon {
    display: flex;
    cursor: pointer;
    width: 100%;
    background-color: transparent;
    border: none;
    opacity: 0.7;
    transition: 0.2s opacity ease;
    padding: 8px;
    border: 2px solid transparent;
    .iconify {
      font-size: 20px;
    }
    position: relative;
    z-index: 1001;

    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 0.5;
    }

    &.selected {
      color: var(--primary-color);
      opacity: 1;
    }
  }

  &.sidebar-left {
    // box-shadow: 4px 0px 18px 0px #0000001c;

    .sidebar-item-icon {
      &.selected {
        border-left: 2px solid var(--primary-color);
      }
      &.disabled-cursor {
        cursor: not-allowed !important;
      }
      &.sidebar-hide-button {
        display: none !important;
      }
    }
  }

  &.sidebar-right {
    // box-shadow: -4px 0px 18px 0px #0000001c;

    .sidebar-item-icon {
      &.selected {
        border-right: 2px solid var(--primary-color);
      }
      &.disabled-cursor {
        cursor: not-allowed !important;
      }
      &.sidebar-hide-button {
        display: none !important;
      }
    }
  }
}

.sidebar-right-drawer {
  top: 40px;
  right: 41px;
}
