.create-lookup-table {
  width: 100%;
  padding: 24px;

  @max-width: 700px;
  overflow-y: auto;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: @max-width;

    h1 {
      font-size: 13px;
      text-transform: uppercase;
      font-weight: bold;
      margin: 0px;
    }

    button {
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 500;
    }
  }

  .form {
    margin-top: 36px;
    max-width: @max-width;

    p.label {
      width: 100%;
    }

    label {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      color: #a7a7a7;
    }

    .spacer {
      height: 36px;
    }
  }

  .csv-dropzone {
    height: 200px;
    background-color: var(--primary-color-opacity-10);
    border: 2px solid transparent;
    border-radius: 6px;
    text-align: center;
    padding: 36px 0px;
    margin-top: 8px;
    transition: 0.3s all ease;
    font-size: 12px;
    color: var(--primary-color);
    display: flex;
    justify-content: center;
    align-items: center;

    &.drop-active {
      border-color: var(--primary-color);
      background: var(--primary-color-opacity-20);
    }
  }

  .icon-button {
    font-size: 12px;
    gap: 4px;
  }

  .csv-table {
    margin-top: 8px;
    width: 90vw;
    max-width: 100%;
    overflow: auto;

    &.expanded {
      max-width: 90vw;
    }
  }
}

.table-title-cell {
  display: flex;
  align-items: center;

  .table-title-input {
    min-width: 5rem;
    max-width: 400px;
  }

  .table-title-text {
    min-width: max-content;
  }

  svg {
    margin-left: 6px;
  }

  .highlighted-title {
    font-weight: bold;
  }

  .table-title-checkBox {
    margin-right: 5px;
    margin-bottom: 5px;
  }
}

.lookup-table-hint-text {
  font-size: 12px;
}
