.error-list-item {
  width: 100%;
  background-color: var(--background-color-elevation-5);
  font-size: 13px;
  cursor: pointer;

  &:hover {
    background-color: var(--primary-color-opacity-20);
  }

  .nodes {
    color: var(--danger-color);
  }

  .description {
    color: var(--foreground-color);
  }

  .error-desc-bold {
    font-weight: 700;
  }
}

.error-list-item-selected-bg {
  width: 100%;
  font-size: 13px;
  background-color: var(--primary-color-opacity-50);
  cursor: pointer;

  .nodes {
    color: var(--danger-color);
  }

  .description {
    color: var(--foreground-color);
  }

  .error-desc-bold {
    font-weight: 700;
  }
}
