@import '../../styles/lessUtils.less';

.box {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  gap: 5px;
  font-size: 12px;

  height: 31px;

  .text-input-mixin();
}

.label {
  width: 86px;
  height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #a7a7a7;
}

.text {
  width: 100%;
  height: 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 10px;
  color: #666666;
  align-self: flex-end;
}

.edit {
  position: relative;
  left: 0%;
  top: 5%;
}

.edit:hover {
  background-color: transparent;
  color: #00b2ff;
}

.editDiv {
  display: inline-flex;
  align-items: center;
}

.text-input-required-marker {
  color: var(--danger-color);
}

.input-error-message-prompt {
  font-size: 12px;
}
