.panel {
  background-color: var(--background-color-elevation-5);
  outline: 1px solid var(--panel-border-color);

  &.source-mapper-panel {
    background-color: var(--background-color);
  }
}

.panel-handle-top {
  z-index: 100;

  &::before {
    // For the on-hover, to indicate dragging interaction.
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 25%;
    border-bottom: 3px solid transparent;
    padding-left: 5px;
    transition: 0.3s border ease 0.3s;
  }
  &:hover,
  &:active {
    &::before {
      border-bottom: 2px solid var(--draggable-border-highlight-color);
    }
  }
}

.panel-handle-right {
  z-index: 100;
